.section1 {
  color: white;
  height: 75vh;
  width: 100%;
  z-index: 1;
}

.backgroundDiv {
  border-bottom-left-radius: 100px;
  height: 100%;
}

.section1flex {
  background-image: url(/08_BlogrLandingPage/static/media/bg-pattern-intro.bf6b3e1b.svg);
  background-size: 210% 590%;
  background-repeat: no-repeat;
  background-position: 23% 51%;
  border-bottom-left-radius: 100px;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.navbar {
  padding: 0 12em;
  width: 100%;
}

.flexcon {
  justify-content: space-between;
  padding: 3em 0 1em 0;
}

.flexcon,
.navLeft,
.navRight {
  align-items: center;
  display: flex;
}

.siteLogo {
  background-image: url(/08_BlogrLandingPage/static/media/logo.1591bc20.svg);
  background-size: auto;
  background-repeat: no-repeat;
  height: 3em;
  width: 8em;
}

.navLink {
  align-items: center;
  position: relative;
}

.navLink a,
.login {
  cursor: pointer;
  font-weight: 600;
  opacity: 0.8;
}

.navLink a:hover {
  text-decoration: underline;
  opacity: 1;
}

.navLink:hover .arrow {
  opacity: 1;
  transform: rotate(180deg) translate(0, 10%);
}

.navLink:hover .dropdown {
  display: block;
}

.arrowDiv {
  display: inline-block;
}

.arrow {
  color: black;
  height: 10px;
  margin-left: 5px;
  opacity: 0.8;
  padding: 0px 7px;
  width: 25px;
}

/* -------Login & Signup */

.desktopLogin,
.desktopSignup,
.mobileLogin,
.mobileSignup {
  font-weight: 600;
}

.desktopLogin {
  color: #fff;
  margin-right: 2em;
  opacity: 0.8;
}

.desktopLogin:hover {
  opacity: 1;
}

.desktopSignup {
  background-color: #fff;
  color: hsl(353, 100%, 62%);
}

.desktopSignup,
.mobileSignup {
  border-radius: 40px;
  padding: 0.6em 1.8em;
}

.mobileLogin,
.mobileSignup {
  display: none;
}

.dropdownDiv {
  height: 10px;
}

/* -------Dropdown */

.dropdown {
  animation: drop 0.4s ease 0.5s;
  background-color: #fff;
  border-radius: 3px;
  color: #000;
  display: none;
  font-size: 14px;
  padding: 1.8em;
  width: 10em;
  position: absolute;
  top: 100%;
  left: -25%;
}

.dropdown li a:hover {
  font-weight: 600;
  text-decoration: none;
}

/* -------Hamburger menu */

.hamburger {
  align-items: center;
  display: flex;
  display: none;
  height: 1.7em;
}

.open {
  background-image: url(/08_BlogrLandingPage/static/media/icon-hamburger.68867946.svg);
  background-repeat: no-repeat;
  height: 1.2em;
  width: 1.7em;
}

.close {
  background-image: url(/08_BlogrLandingPage/static/media/icon-close.c6580312.svg);
  height: 1.6em;
  width: 1.6em;
}

/* -------Content Section */

.contentSec1 {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 70%;
  justify-content: center;
}

.contentButton {
  display: flex;
  justify-content: center;
}

.startForFree,
.learnMore {
  border-radius: 40px;
  padding: 0.6em 1.2em;
}

.startForFree {
  background-color: #fff;
  color: hsl(353, 100%, 62%);
  font-weight: 600;
}

.desktopSignup:hover,
.startForFree:hover {
  background-color: rgba(255, 255, 255, 0.3);
  color: white;
}

.learnMore {
  background-color: transparent;
  border: 1px solid white;
  color: white;
}

.learnMore:hover {
  background-color: white;
  color: hsl(13, 100%, 72%);
}

.mobileMenuDiv {
  display: none;
}

/* ======================================== */

@media (max-width: 1200px) {
  .navbar {
    position: relative;
  }

  .navLink{
    display: none;
  }

  .hamburger {
    display: block;
  }

  .desktopUL {
    display: none;
  }

  .mobileMenuDiv {
    color: #000;
    display: flex;
    flex-direction: column;
    height: 20em;
    text-align: center;
    width: 90%;
    position: absolute;
    left: 5%;
  }

  .mobileMenuUL {
    background-color: #fff;
    border-radius: 7px;
    margin-bottom: 1em;
    padding: 2em;
  }

  .mobileNavLink:nth-child(1),
  .mobileNavLink:nth-child(2) {
    margin-bottom: 10px;
  }

  .mobileNavLink:hover .arrow {
    transform: rotate(180deg) translate(0, 10%);
  }

  .mobileNavLink:hover .mobileDropdown {
    display: block;
  }

  .mobileNavLink a,
  .mobileDropdown a {
    font-weight: 600;
  }

  .mobileNavLink a:hover {
    color: hsl(0, 0%, 33%);
    text-decoration: underline;
  }

  .mobileDropdown a:hover {
    color: #000;
    text-decoration: none;
  }

  .mobileDropdownDiv {
    padding: 10px 0;
  }

  .mobileDropdown {
    background-color: hsl(0, 0%, 91%);
    border-radius: 7px;
    color: hsl(0, 0%, 33%);
    display: none;
    padding: 1em 1em;
  }

  .desktopLoginSignupDiv {
    display: none;
  }

  .mobileLoginSignupDiv {
    border-top: solid hsl(0, 0%, 91%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2em 2em;
    width: 100%;
  }

  .desktopLogin,
  .desktopSignup {
    display: none;
  }

  .mobileLogin,
  .mobileSignup {
    font-weight: 600;
  }

  .mobileLogin {
    color: black;
    display: block;
  }

  .mobileLogin:hover {
    color: hsl(0, 0%, 33%);
  }

  .mobileSignup {
    border-radius: 40px;
    color: white;
    display: block;
  }
}

